import React, { Component } from 'react'
import styles from './index.scss'

function GameOver(props){
    return (
        <div className={styles.gameover}>
            <p>!</p>
            <div>
                <button className={styles.gamebtn} onClick={props.tryagain}></button>
            </div>
        </div>
    )
}
export default GameOver


